* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list,
.country-card {
  list-style: none;
  padding-left: 0;
}
.country-item {
  display: flex;
  align-items: center;
  background-color: #dee25c92;
}

.country-info-item {
  display: flex;
  align-items: center;
}

.country-item:not(:last-child) {
  margin-bottom: 8px;
}

.country-flag {
  margin-right: 10px;
}

.country-name {
  margin: 0;
}
